
import header from '../public/header'
import footer from '../public/footer'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'


export default {
	
  components: {
  	headerV : header,
  	footerV : footer,
    Grid,GridItem
  },
  data () {
    return {
        primenu : true,
        none : false,
        tow : false,
        four : false,
        five : false,
        six : false
    }
  },
  methods : {
    next (obj){
        if(obj == 'none'){
            this.none = true;
            this.primenu = false;
        }else if(obj == 'tow'){
            this.tow = true;
            this.primenu = false;
        }else if(obj == 'four'){
            this.four = true;
            this.primenu = false;
        }else if(obj == 'five'){
            this.five = true;    
            this.primenu = false;       
        }else if(obj == 'six'){
            this.six = true;
            this.primenu = false;
        }
    },
    prev (obj){
        if(obj == 'none'){
            this.none = false;
            this.primenu = true;
        }else if(obj == 'tow'){
            this.tow = false;
            this.primenu = true;
        }else if(obj == 'four'){
            this.four = false;
            this.primenu = true;
        }else if(obj == 'five'){
            this.five = false;    
            this.primenu = true;       
        }else if(obj == 'six'){
            this.six = false;
            this.primenu = true;
        }
    }
  }
}



